<script>
  import {onMount} from 'svelte';
  import Quagga from '@ericblade/quagga2'; // ES6
  let results;
  const state = {
    inputStream: {
      type : "LiveStream",
    },
    locator: {
      patchSize: "small",
      halfSample: false
    },
    numOfWorkers: 4,
    frequency: 10,
    decoder: {
      readers: ["upc_reader", "ean_reader", "code_128_reader",
      'code_39_reader', 'code_93_reader'],
    },
    /*
    decoder: {
      readers : [{
        format: "upc_reader",
        config: {}
      }]
    },
     */
    locate: true
  }
  console.log('Quagga', Quagga);
  function detected(data) {
    console.log(data.codeResult);
    results = JSON.stringify(data.codeResult)
  }
  onMount( ()=>{
    Quagga.init(state, function(err) {
      if (err) {
        console.log(err);
        return
      }
      console.log("Initialization finished. Ready to start");
      Quagga.start();
      Quagga.onDetected(detected);
      Quagga.onProcessed(function(result) {
        var drawingCtx = Quagga.canvas.ctx.overlay,
          drawingCanvas = Quagga.canvas.dom.overlay;

        if (result) {
          if (result.boxes) {
            drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
            result.boxes.filter(function (box) {
              return box !== result.box;
            }).forEach(function (box) {
              Quagga.ImageDebug.drawPath(box, {x: 0, y: 1}, drawingCtx, {color: "green", lineWidth: 2});
            });
          }

          if (result.box) {
            Quagga.ImageDebug.drawPath(result.box, {x: 0, y: 1}, drawingCtx, {color: "#00F", lineWidth: 2});
          }

          if (result.codeResult && result.codeResult.code) {
            Quagga.ImageDebug.drawPath(result.line, {x: 'x', y: 'y'}, drawingCtx, {color: 'red', lineWidth: 3});
          }
        }
      });

    });
  });
  
  
</script>
results: {results}
<div id="interactive" class="viewport"></div>
